import Header from '@/components/header/header.vue'
import Comment from '@/components/comment/comment.vue'
import CommentList from '@/components/commentList/commentList.vue'
import {mapState} from 'vuex'
import {get,post} from '@/common/methods.js'
import {ImgUrl} from '@/common/common.js'
export default {
	data() {
		return {
			title: "",
			bookinfo: "",
			ImgUrl: "",
			allDiss: 0,
			comList: [],
			bid: 0
		};
	},
	computed:{
		...mapState([
			"userid"
		])
	},
	onLoad(e){
		this.ImgUrl = ImgUrl;
		let bid = e.id;
		this.bid = e.id;
		get("/book/book/"+bid+"?associatorid="+this.userid).then(res=>{
			if(res.status == 200){
				this.bookinfo = res.data.bookinfo;
			}
			// console.log(res)
		})
		get("/book/book/comment/"+bid+"?associatorid="+this.userid).then(res=>{
			if(res.status == 200){
				this.allDiss = res.data.totalCommentNums;
				this.comList = res.data.gradeList;
			}
		})
		
	},
	methods:{
		showComments(){
			uni.navigateTo({
				url:"pages/addComment/addComment"
			})
		},
		goRead(id){
			console.log(this.bid)
			get("/book/book/getBookPath/"+this.bid,{}).then(res=>{
				console.log(res)
				if(res.status == 200){
					if(res.data.type == 1){
						uni.navigateTo({
							url: '/pages/books/readBook2?bookpath='+res.data.path+'&total='+res.data.totalCount
						});
					}else{
						uni.navigateTo({
							url:"/pages/books/readBook?bookpath="+res.data.path
						})
					}
				}
			})
			
		},
		
	},
	components:{
		Header,
		Comment,
		CommentList
	}
}
